import * as React from 'react';
import { Controller } from '@ali/4ever-cangjie';
import { Button, VedioNormal } from '@ali/we-design';
;
import { insertOnlineVideo } from '../actions';

export interface OnlineVideoMenuProps {
  controller: Controller;
  locale: any;
}

export default function OnlineVideoButton(props: OnlineVideoMenuProps) {
  const { controller, locale } = props;
  const isDisabled = controller.query('isSelectionInTable', { excludeColumns: true })
    || controller.query('isSelectionInImage')
    || controller.query('isSelectionInLinkCard');
  const handleClick = () => {
    if (isDisabled) {
      return;
    }
    controller.run('onAction', insertOnlineVideo());
  };

  return (
    <Button
      title={locale.buttonTitle}
      bordered={false}
      testid="bi-toolbar-onlinevideo"
      icon={<VedioNormal />}
      onClick={handleClick}
    />
  );
}
